$backgroundColor: #070d41;
$color1: white;
$color2: #8e92b5;
$color3: #f460b9;
$color4: #1040fa;
$color5: #993682;
$color6: #722466;

* {
  padding: 0;
  margin: 0;
}
#player {
  height: 100vh;
  background: $backgroundColor;
  overflow: hidden;
  font-family: 楷体;

  .header {
    height: 110px;
    text-align: center;
    h1 {
      color: $color1;
      font-size: 24px;
      padding-top: 20px;
    }
    p {
      font-weight: bold;
      padding-top: 10px;
      color: $color2;
      font-size: 14px;
    }
    .balls {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 20px;
      span {
        display: block;
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: $color6;
        margin: 0 4px;

        &.current {
          width: 8px;
          height: 8px;
          background-color: $color3;
        }
      }
    }
  }
  .panels {
    width: 200vw;
    display: flex;
    transition: transform 0.3s;
    height: calc(100vh - 270px);
    align-items: center;
    &.panel1 {
      transform: translateX(0vw);
    }
    &.panel2 {
      transform: translateX(-100vw);
    }
    .panel-effect {
      width: 100vw;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      .effect {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 40vh;
        > div {
          border-radius: 50%;
          background-size: contain;
          position: absolute;
        }
        .effect-1 {
          background: url(../svg/effect-no-move.svg) 0 0 repeat;
          width: 70vw;
          height: 70vw;
          animation: rotate 10s linear infinite;
        }
        .effect-2 {
          background: url(../svg/effect-move1.svg) 0 0 repeat;
          width: 60vw;
          height: 60vw;
          animation: rotate 15s linear infinite reverse;
        }
        .effect-3 {
          background: url(../svg/effect-move2.svg) 0 0 repeat;
          width: 24vw;
          height: 24vw;
          animation: rotate 20s linear infinite;
        }
      }
      .lyric {
        height: 54px;
        text-align: center;
        p {
          font-size: 16px;
          color: $color2;
          margin-top: 8px;

          &:nth-child(1) {
            font-size: 18px;
          }

          &.current {
            color: $color1;
          }
        }
      }
    }
    .panel-lyrics {
      width: 100vw;
      height: 100%;
      text-align: center;
      line-height: 2;
      overflow: hidden;
      .container {
        transition: all 3s;
        margin-top: 150px;
        transform: translateY(0);
        
        p {
          font-size: 16px;
          color: $color2;

          &.current {
            color: $color1;
          }
        }
      }
    }
  }
  .footer {
    height: 160px;
    .buttons {
      margin-top: 10px;
      display: flex;
      justify-content: center;
      svg {
        width: 24px;
        height: 24px;
        margin: 0 20px;
      }
    }
    .area-bar {
      color: $color1;
      font-size: 12px;
      display: flex;
      padding: 0 20px;
      margin-top: 20px;
      align-items: center;
      .time-start {
        order: 1;
      }
      .time-end {
        order: 3;
      }
      .bar {
        order: 2;
        flex: 1;
        height: 4px;
        background-color: $color4;
        border-radius: 2px;
        margin: 0 20px 0 10px;
        .progress {
          width: 0;
          height: 100%;
          border-radius: 2px;
          background-color: $color3;
          position: relative;

          &::after {
            content: "";
            position: absolute;
            right: -15px;
            top: -7px;
            width: 16px;
            height: 16px;
            background: url(../svg/progress.svg) 0 0 no-repeat;
            background-size: contain;
          }
        }
      }
    }
    .actions {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 20px;
      padding: 0 20px;

      .btn-play-pause {
        width: 50px;
        height: 50px;
        background-size: contain;
        &.pause {
          background-image: url(../svg/播放.svg);
        }
        &.playing {
          background-image: url(../svg/暂停.svg);
        }
      }
      img {
        width: 28px;
        height: 28px;
      }
    }
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
